.cart-row{
  font-size:18px;
  display:block;

  
}
.cart-header{
  font-size:1.2rem;
  font-weight: bold;
  color:#777;
  

}
.cart-item{
  width:45%;
  
  

}
.cart-img{
  width:80px;
  

}
.cart-price{
  width:20%;

  
}
.cart-quantity{
  width:35%;

 
}
.cart-column{
  display: flex;
  
  align-items: center;
  border-bottom: 1px solid #777;
  margin-right: 1.5em;
  padding-bottom: 10px;
  margin-top: 10px;
}


.cart-quantity button{
  color:white;
  font-weight: bold;
  background: rgb(104, 154, 154);
  cursor: pointer;
  border:none;
  border-radius:8%;
  height:30px;
  outline:none;
  padding: 3px 7px;
  margin-left: 10px;
  margin-right: 0px;
}
.cart-quantity button:hover{
  background:rgb(62, 105, 105);
}
.cart-total{
  display:flex;
  flex-direction: row-reverse;
  font-size:1.2rem;
  margin:20px;
}
.cart-total .total-title{
  margin-right:10px;
  font-size:1.5rem;
}

.purchase-btn{
  display: flex;
  align-items: center;
  justify-content: center;
  margin:auto;
  margin-top: 50px;
  background: rgb(104, 154, 154);
  color:white;
  font-size:1.2rem;
  font-weight: bold;
  border:none;
  border-radius:8%;
  padding:12px;
  cursor: pointer;
}
.purchase-btn:hover{
  background-color:rgb(62, 105, 105);
}
.container {
  position: fixed;
  bottom: 10px;
  right: 10px;
  display: flex;
  flex-direction: column;
}
.amount{
    
  border: 1px solid #ccc;
  font-family: 'Metal Mania';
 font-size: small;
  color: #777;
}
.cart-items{
 list-style: none;

  
  
}