.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 20;
  background-color: rgba(0, 0, 0, 0.75);
}

.modal {
  position: fixed;
  height: 100%;
  right: 0;
  border-radius: 2px;
  padding-left: 15px;
  display: block;
  

  padding-top: 15px;

  background-color: white;

  z-index: 20;
}



@keyframes slide-down {
  from {
    opacity: 0;
    transform: translateY(-3rem);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
