.cart-row {
  font-size: 18px;
  display: flex;
 
  
}

.cart-header {
  font-size: 1.2rem;
  font-weight: bold;
  text-align: center;
  padding-top: 10px;
  font-family: "Metal Mania";
}
@media (max-width: 600px) {
  header li {
    margin: 0px 20px;
  }
}
.cart-item {
  width: 45%;

  font-family: "Metal Mania";
  list-style: none;
  color:#777;

  display: flex;
  justify-content: space-between;
}
.cart-img {
  width: 80px;
  border-radius: 8%;
  margin-right: 20px;
  color:#777;
}
.cart-price {
  width: 20%;
  font-family: "Metal Mania";
  margin-right: 0em;
  color:#777;
}
.cart-quantity {
  
  font-family: "Metal Mania";
  height: 90px;

  outline: none;
  text-align: center;
  color:#777;
  font-family: "Metal Mania";
  margin-right: 0px;
}
.cart-column {
  display: flex;

  align-items: center;
  border-bottom: 1px solid #777;
  margin-right: 2.5em;
  padding-bottom: 2px;
  margin-top: 20px;
  list-style: none;
}


.button {
  color: white;
  font-weight: bold;
  background: rgb(231, 76, 76);
  cursor: pointer;
  border: none;
  border-radius: 8%;
  height: 30px;
  outline: none;
  margin-left: 0px;
}
.button:hover {
  background: rgb(218, 37, 37);
}
.cart-total {
  display: flex;
  flex-direction: row-reverse;
  font-size: 1.2rem;
  margin: 20px;
  font-family: "Metal Mania";
  color:#777;
}
.cart-total .total-title {
  margin-right: 10px;
  font-size: 1.5rem;
  font-family: "Metal Mania";
  color:#777;
  
}

.purchase-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
  margin-top: 50px;
  background:rgb(104, 154, 154);
  color: white;
  font-size: 1.2rem;
  font-weight: bold;
  border: none;
  border-radius: 8%;
  padding: 12px;
  cursor: pointer;
  font-family: "Metal Mania";
}
.purchase-btn:hover {
  background-color:rgb(65, 113, 113);
}
.container {
  position: fixed;
  bottom: 10px;
  right: 10px;
}
.cancel {
  position: absolute;
  right: 15px;
  padding-right: 5px;
  color:white;
  font-weight: bold;
  font-size: 20px;
  font-family: "Metal Mania";
  background-color: rgb(104, 154, 154);
}
.cancel:hover {
  background-color:rgb(65, 113, 113) ;
}
.title {
  width: 45%;
  margin-left: 30px 0px;
  font-family: "Metal Mania";
  display: block;
  align-items: center;
  border-bottom: 1px solid #777;
  margin-right: 1.5em;
  padding-bottom: 10px;
  margin-top: 10px;
  list-style: none;
}
.price {
  width: 20%;
  font-family: "Metal Mania";
  display: block;
  align-items: center;
  border-bottom: 1px solid black;
  margin-right: 1.5em;
  padding-bottom: 20px;
  margin-top: 10px;
  list-style: none;
}
.quantity {
  width: 35%;
  font-family: "Metal Mania";
  display: block;
  align-items: center;
  border-bottom: 1px solid black;
  margin-right: 1.5em;
  padding-bottom: 10px;
  margin-top: 10px;
  list-style: none;
}
.input {
  width: 3rem;
}
.amount {
  border: 1px solid #ccc;
  font-family: "Metal Mania";
  font-size: 15px;
  color: black;
  padding: 5px;
  margin-left:10px
  
}
.cart-items{
   list-style: none;
    
}
